<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right" class="bread">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-table :data="goodsList" style="width: 100%" stripe height="700px">
      <el-table-column
        prop="goodsId"
        label="ID"
        width="100px"
      ></el-table-column>
      <el-table-column prop="companyName" label=""></el-table-column>
      <el-table-column prop="supply" label=""></el-table-column>
      <el-table-column prop="proArea" label=""></el-table-column>
      <el-table-column
        prop="goodsPrice"
        label="/"
        width="130px"
      ></el-table-column>
      <el-table-column prop="transport" label=""></el-table-column>
      <el-table-column
        prop="transPrice"
        label="/"
        width="130px"
      ></el-table-column>
      <el-table-column prop="quality" label=""></el-table-column>
      <el-table-column
        prop="qualityCertificate"
        label=""
        width="300px"
      >
        <template slot-scope="scope">
          <el-link
            type="primary"
            @click="downloadmyfile(scope.row.qualityCertificate)"
            >{{ scope.row.qualityCertificate }}</el-link
          >
        </template>
      </el-table-column>
      <el-table-column prop="state" label="">
        <template slot-scope="scope">
          <el-tag type="danger" v-if="scope.row.state === '1'"></el-tag>
          <el-tag type="success" v-else></el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="opinion" label=""></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button
            type="primary"
            icon="el-icon-s-goods"
            size="mini"
            :disabled="scope.row.state === '1'"
            slot="reference"
            @click="buygoods(scope.row.goodsId)"
            ></el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <div class="block">
      <el-pagination
        layout="prev, pager, next"
        :page-size="2"
        :total="total"
        @current-change="handleCurrentChange"
        :current-page="currentpage"
      >
      </el-pagination>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      goodsList: [],
      //  http://127.0.0.1:8081/file/download?fileName=11.zip
      total: 0,
      currentpage: 0,
      pagenum: 0,
      dialogFormVisible: false,
      form: {
        info: ''
      },
      // true 
      value: true,
      // ID
      checkId: 0
    }
  },
  methods: {
    // 
    async getList () {
      // 
      const { data: res } = await this.$http.post('goods/getallgoods',
        {
          pagenum: this.pagenum
        })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.goodsList = res.data.goodsList
      this.total = res.data.total
    },
    handleCurrentChange (num) {
      this.pagenum = num
      this.getList()
    },
    checkinfo (id) {
      this.checkId = id
      this.dialogFormVisible = true
    },
    // 
    clearDiolog () {
      this.dialogFormVisible = false
      this.checkId = 0
      this.form.info = ''
    },
    // 
    async checkInfoById () {
      this.dialogFormVisible = false
      console.log(this.checkId)
      console.log(this.form.info)
      console.log(this.value)
      const { data: res } = await this.$http.post('infor/modifystate', {
        informationid: this.checkId,
        state: this.value ? 1 : 2,
        opinion: this.form.info
      })
      console.log(res)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.getList()
      this.checkId = 0
      this.form.info = ''
      return this.$message.success('')
    },
    downloadmyfile (file) {
      window.location.href = `http://121.5.78.195:8081/file/download?fileName=${file}`
    },
    async buygoods (id) {
      const data = new Date()
      const ids = data.getHours() + '' + data.getMilliseconds() + data.getSeconds() + data.getMinutes()
      const { data: res } = await this.$http.post('order/addorder', {
        agreementid: ids,
        goodsid: id,
        acompany: '',
        bcompany: ''
      })
      if (res.mata.status !== 200) {
        return this.$message.error('')
      }
      const { data: ref } = await this.$http.post('goods/modifySAndO', {
        goodsid: id,
        state: '1',
        opinion: ''
      })
      if (ref.meta.status !== 200) {
        return this.$message.error('')
      }
      this.getList()
      this.myvisible = false
      return this.$message.success('')
    }
  },
  created () {
    if (window.sessionStorage.getItem('loginstate') !== '1') {
      this.$router.push('/')
      return this.$message.error(',')
    }
    this.getList()
  }
}
</script>
<style scoped>
.bread {
  margin-bottom: 20px;
}
</style>
