<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right" class="bread">
      <el-breadcrumb-item :to="{ path: '/index' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-table :data="inforList" style="width: 100%" stripe height="700px">
      <el-table-column
        prop="informationId"
        label="ID"
        width="100px"
      ></el-table-column>
      <el-table-column
        prop="informationTitle"
        label=""
      ></el-table-column>
      <el-table-column
        prop="informationSample"
        label=""
      ></el-table-column>
      <el-table-column
        prop="informationContent"
        label=""
        width="400px"
      ></el-table-column>
      <el-table-column prop="state" label="">
        <template slot-scope="scope">
          <el-tag v-if="scope.row.state == 0"></el-tag>
          <el-tag type="success" v-if="scope.row.state == 1"></el-tag>
          <el-tag type="danger" v-if="scope.row.state == 2"></el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="opinion" label=""></el-table-column>
      <el-table-column label=""
        ><template slot-scope="scope">
          <el-button
            type="primary"
            icon="el-icon-edit"
            size="mini"
            @click="checkinfo(scope.row.informationId)"
            ></el-button
          >
        </template></el-table-column
      >
    </el-table>
    <div class="block">
      <el-pagination
        layout="prev, pager, next"
        :page-size="2"
        :total="total"
        @current-change="handleCurrentChange"
        :current-page="currentpage"
      >
      </el-pagination>
    </div>
    <!--  -->
    <el-dialog title="" :visible.sync="dialogFormVisible" width="500px">
      <el-switch
        style="display: block"
        v-model="value"
        active-color="#13ce66"
        inactive-color="#ff4949"
        active-text=""
        inactive-text=""
      >
      </el-switch>
      <el-input
        type="textarea"
        :rows="2"
        placeholder=""
        class="elinputit"
        v-model="form.info"
        :disabled="value"
      >
      </el-input>
      <div slot="footer" class="dialog-footer">
        <el-button @click="clearDiolog"> </el-button>
        <el-button type="primary" @click="checkInfoById"> </el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data () {
    return {
      inforList: [],
      // 
      total: 0,
      currentpage: 0,
      pagenum: 0,
      dialogFormVisible: false,
      form: {
        info: ''
      },
      // true 
      value: true,
      // ID
      checkId: 0
    }
  },
  methods: {
    // 
    async getList () {
      // 
      const { data: res } = await this.$http.post('infor/getallinfor',
        {
          pagenum: this.pagenum
        })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.inforList = res.data.inforList
      this.total = res.data.total
    },
    handleCurrentChange (num) {
      this.pagenum = num
      this.getList()
    },
    checkinfo (id) {
      this.checkId = id
      this.dialogFormVisible = true
    },
    // 
    clearDiolog () {
      this.dialogFormVisible = false
      this.checkId = 0
      this.form.info = ''
    },
    // 
    async checkInfoById () {
      this.dialogFormVisible = false
      console.log(this.checkId)
      console.log(this.form.info)
      console.log(this.value)
      const { data: res } = await this.$http.post('infor/modifystate', {
        informationid: this.checkId,
        state: this.value ? 1 : 2,
        opinion: this.form.info
      })
      console.log(res)
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.getList()
      this.checkId = 0
      this.form.info = ''
      return this.$message.success('')
    }
  },
  created () {
    if (window.sessionStorage.getItem('loginstate') !== '1') {
      this.$router.push('/')
      return this.$message.error(',')
    }
    this.getList()
  }
}
</script>
<style scoped>
.bread {
  margin-bottom: 20px;
}
.elinputit {
  margin-top: 20px;
}
</style>
