<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right" class="bread">
      <el-breadcrumb-item :to="{ path: '/index' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-table :data="orderList" style="width: 100%" stripe height="700px">
      <el-table-column
        prop="orderId"
        label="ID"
        width="100px"
      ></el-table-column>
      <el-table-column prop="agreementId" label=""></el-table-column>
      <el-table-column prop="goodsId" label="ID"></el-table-column>
      <el-table-column prop="state" label=""></el-table-column>
      <el-table-column prop="state" label="">
        <template slot-scope="scope">
          <el-tag type="success" v-if="scope.row.state === 'true'"
            ></el-tag
          >
          <el-tag type="danger" v-else></el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="aCompany" label=""></el-table-column>
      <el-table-column prop="bCompany" label=""></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope">
          <el-button
            type="primary"
            @click="changestate(scope.row.orderId, scope.row.state)"
            size="mini"
            :disabled="scope.row.state === 'true'"
            ></el-button
          >
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-pagination
      layout="prev, pager, next"
      :page-size="2"
      :total="total"
      @current-change="handleCurrentChange"
      :current-page="currentpage"
    >
    </el-pagination>
  </div>
</template>
<script>
export default {
  data () {
    return {
      // 
      orderList: [],
      total: 0,
      currentpage: 0,
      pagenum: 0,
      options: []
    }
  },
  methods: {
    async getAllList () {
      const { data: res } = await this.$http.post('order/getallorder', {
        pagenum: this.pagenum
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.orderList = res.data.orderList
      this.total = res.data.total
      return true
    },
    handleCurrentChange (num) {
      this.pagenum = num
      this.getAllList()
    },
    changestate (id, state) {
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        const { data: res } = await this.$http.post('order/modifystate', {
          orderid: id,
          state: state === 'true' ? 'false' : 'true'
        })
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        this.getAllList()
        this.$message({
          type: 'success',
          message: ''
        })
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    }
  },
  created () {
    if (window.sessionStorage.getItem('loginstate') !== '1') {
      this.$router.push('/')
      return this.$message.error(',')
    }
    this.getAllList()
  }
}
</script>
<style scoped>
.bread {
  margin-bottom: 20px;
}
</style>
