<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right" class="bread">
      <el-breadcrumb-item :to="{ path: '/index' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-table :data="agreementList" style="width: 100%" stripe height="700px">
      <el-table-column
        prop="agreementId"
        label="ID"
        width="180px"
      ></el-table-column>
      <el-table-column
        prop="aCompany"
        label=""
        width="180px"
      ></el-table-column>
      <el-table-column
        prop="bCompany"
        label=""
        width="180px"
      ></el-table-column>
      <el-table-column
        prop="transport"
        label=""
        width="180px"
      ></el-table-column>
      <el-table-column
        prop="price"
        label="()"
        width="120px"
      ></el-table-column>
      <el-table-column prop="time" label="" width="220px"></el-table-column>
      <el-table-column prop="state" label="" width="100px">
        <template slot-scope="scope">
          <el-tag type="success" v-if="scope.row.state == 1"></el-tag>
          <el-tag type="info" v-else-if="scope.row.state == 0"></el-tag>
          <el-tag type="danger" v-else></el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="opinion" label="">
        <template slot-scope="scope">
          <el-input
            :placeholder="scope.row.opinion"
            v-model="options[scope.row.agreementId]"
          ></el-input>
        </template>
      </el-table-column>
      <el-table-column label="" width="150px">
        <template slot-scope="scope">
          <el-button
            type="success"
            icon="el-icon-check"
            circle
            size="mini"
            v-if="scope.row.state != 1"
            @click="dealhetong(scope.row.agreementId)"
          ></el-button>
          <el-button
            type="danger"
            icon="el-icon-close"
            circle
            size="mini"
            v-if="scope.row.state != 2"
            @click="dangerhetong(scope.row.agreementId)"
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <!--  -->
    <el-pagination
      layout="prev, pager, next"
      :page-size="2"
      :total="total"
      @current-change="handleCurrentChange"
      :current-page="currentpage"
    >
    </el-pagination>
  </div>
</template>
<script>
export default {
  data () {
    return {
      // 
      agreementList: [],
      total: 0,
      currentpage: 0,
      pagenum: 0,
      options: []
    }
  },
  methods: {
    async getAllList () {
      const { data: res } = await this.$http.post('agreement/getallagreement', {
        pagenum: this.pagenum
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.agreementList = res.data.agreementList
      this.total = res.data.total
    },
    handleCurrentChange (num) {
      this.pagenum = num
      this.getAllList()
    },
    // 
    async dealhetong (id) {
      console.log(id)
      const { data: res } = await this.$http.post('agreement/modifyagreementstate', {
        agreementid: id,
        state: 1,
        opinion: typeof (this.options[id]) === 'undefined' ? '' : this.options[id]
      })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.options = []
      this.getAllList()
      return this.$message.success('')
    },
    // 
    dangerhetong (id) {
      console.log(id)
      console.log(this.options[id])
      this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).then(async () => {
        const { data: res } = await this.$http.post('agreement/modifyagreementstate', {
          agreementid: id,
          state: 2,
          opinion: typeof (this.options[id]) === 'undefined' ? '' : this.options[id]
        })
        if (res.meta.status !== 200) {
          return this.$message.error('')
        }
        this.options = []
        this.getAllList()
        return this.$message.success('')
      }).catch(() => {
        this.$message({
          type: 'info',
          message: ''
        })
      })
    }
  },
  created () {
    if (window.sessionStorage.getItem('loginstate') !== '1') {
      this.$router.push('/')
      return this.$message.error(',')
    }
    this.getAllList()
  }
}
</script>
<style scoped>
.bread {
  margin-bottom: 20px;
}
</style>
