<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right" class="bread">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <div class="formarea">
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="">
          <el-input v-model="ruleForm.companyname" disabled></el-input>
        </el-form-item>
        <el-form-item label="" prop="supply">
          <el-input
            v-model="ruleForm.supply"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="proarea">
          <el-input
            v-model="ruleForm.proarea"
            placeholder=""
          ></el-input>
        </el-form-item>
        <el-form-item label="/" prop="goodsprice">
          <el-input
            v-model="ruleForm.goodsprice"
            placeholder=""
            type="number"
          ></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select
            v-model="ruleForm.transport"
            placeholder=""
          >
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="/" prop="transprice">
          <el-input
            v-model="ruleForm.transprice"
            placeholder=""
            type="number"
          ></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select
            v-model="ruleForm.quality"
            placeholder=""
          >
            <el-option
              v-for="item in options2"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            >
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-upload
            class="upload-demo"
            :action="uploadUrl"
            :before-remove="beforeRemove"
            multiple
            :limit="1"
            :on-exceed="handleExceed"
            :file-list="fileList"
            :on-success="successfile"
          >
            <el-button size="small" type="primary"></el-button>
          </el-upload></el-form-item
        >
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')"
            ></el-button
          >
          <el-button @click="resetForm('ruleForm')"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      uploadUrl: 'http://121.5.78.195:8081/file/upload',
      fileList: [],
      options: [{
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }],
      options2: [{
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }, {
        value: '',
        label: ''
      }],
      ruleForm: {
        companyname: '',
        supply: '',
        proarea: '',
        goodsprice: '',
        transport: '',
        transprice: '',
        quality: '',
        qualitycertificate: ''
      },
      rules: {
        supply: [{ required: true, message: '', trigger: 'blur' }],
        proarea: [{ required: true, message: '', trigger: 'blur' }],
        goodsprice: [{ required: true, message: '', trigger: 'blur' }],
        transprice: [{ required: true, message: '', trigger: 'blur' }]
      }
    }
  },
  methods: {
    submitForm (formName) {
      if (this.ruleForm.qualitycertificate === '' || this.ruleForm.transport === '' || this.ruleForm.quality === '') {
        return this.$message.info('')
      }
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          const { data: res } = await this.$http.post('goods/addgoods', {
            companyname: this.ruleForm.companyname,
            supply: this.ruleForm.supply,
            proarea: this.ruleForm.proarea,
            goodsprice: this.ruleForm.goodsprice,
            transport: this.ruleForm.transport,
            transprice: this.ruleForm.transprice,
            quality: this.ruleForm.quality,
            qualitycertificate: this.ruleForm.qualitycertificate
          })
          if (res.mata.status !== 200) {
            return this.$message.error('')
          }
          // this.ruleForm()
          return this.$message.success('')
        } else {
          return this.$message.error('')
        }
      })
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
      this.ruleForm.transport = ''
      this.ruleForm.quality = ''
      return this.$message.info('')
    },
    handleExceed (files, fileList) {
      this.$message.warning(' 1 ')
    },
    beforeRemove (file, fileList) {
      return this.$confirm(` ${file.name}`)
    },
    successfile (res, file, fileList) {
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.ruleForm.qualitycertificate = res.data.name
      return this.$message.success('')
    }
  },
  created () {
    if (window.sessionStorage.getItem('loginstate') !== '1') {
      this.$router.push('/')
      return this.$message.error(',')
    }
  }
}
</script>
<style scoped>
.bread {
  margin-bottom: 20px;
}
.formarea {
  width: 600px;
  margin: 0 auto;
}
</style>
