<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right" class="bread">
      <el-breadcrumb-item :to="{ path: '/index' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <el-table :data="userList" style="width: 100%" stripe height="700px">
      <el-table-column prop="userid" label="ID"></el-table-column>
      <el-table-column prop="userName" label=""></el-table-column>
      <el-table-column prop="userRole" label="">
        <template slot-scope="scope">
          <el-tag v-if="scope.row.userRole === '2'" type="success"
            ></el-tag
          >
          <el-tag v-else-if="scope.row.userRole === '0'" type="info"
            ></el-tag
          >
          <el-tag v-else type="danger"></el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="userState" label="/"
        ><template slot-scope="scope">
          <el-switch
            :value="scope.row.userState === 'true'"
            active-color="#13ce66"
            inactive-color="#ff4949"
            disabled
          >
          </el-switch>
        </template>
      </el-table-column>
      <el-table-column label=""></el-table-column>
    </el-table>
    <div class="block">
      <el-pagination
        layout="prev, pager, next"
        :page-size="2"
        :total="total"
        @current-change="handleCurrentChange"
        :current-page="currentpage"
      >
      </el-pagination>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      // 
      userList: [],
      // 
      total: 0,
      currentpage: 0,
      pagenum: 0
    }
  },
  methods: {
    handleCurrentChange (num) {
      this.pagenum = num
      this.getAllAdminInfo()
    },
    // 
    async getAllAdminInfo () {
      const { data: res } = await this.$http.post('user/getalluser', {
        pagenum: this.pagenum
      })
      if (res.meta.status !== 200) {
        return this.$message.success('')
      }
      console.log(res)
      this.userList = res.data.userList
      this.total = res.data.total
    }
  },
  created () {
    this.getAllAdminInfo()
  }
}
</script>
<style scoped>
.bread {
  margin-bottom: 20px;
}
</style>
