<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right" class="bread">
      <el-breadcrumb-item :to="{ path: '/index' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <el-button
      type="primary"
      icon="el-icon-edit"
      size="mini"
      style="float: right"
      @click="registadmin"
      ></el-button
    >
    <el-table :data="adminList" style="width: 100%" stripe height="700px">
      <el-table-column prop="adminId" label="ID"></el-table-column>
      <el-table-column prop="adminName" label=""></el-table-column>
      <el-table-column prop="password" label=""></el-table-column>
      <el-table-column prop="role" label=""
        ><template slot-scope="scope">
          <el-tag type="danger" v-if="scope.row.role == 0"></el-tag>
          <el-tag v-if="scope.row.role == 1"></el-tag>
          <el-tag type="success" v-if="scope.row.role == 2"></el-tag>
        </template>
      </el-table-column>
    </el-table>
    <div class="block">
      <el-pagination
        layout="prev, pager, next"
        :page-size="2"
        :total="total"
        @current-change="handleCurrentChange"
        :current-page="currentpage"
      >
      </el-pagination>
    </div>
    <!--   -->
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      width="30%"
      :before-close="handleClose"
      ><span>
        <el-form
          :model="registruleForm"
          :rules="rules"
          ref="ruleForm"
          label-width="180px"
          class="demo-ruleForm"
        >
          <el-form-item label="" prop="adminname">
            <el-input v-model="registruleForm.adminname"></el-input>
          </el-form-item>
          <el-form-item label="" prop="password">
            <el-input v-model="registruleForm.password"></el-input>
          </el-form-item>
          <el-form-item label="(1,2)" prop="role">
            <el-input
              v-model="registruleForm.role"
            ></el-input> </el-form-item></el-form
      ></span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="addAdmins"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data () {
    return {
      // 
      dialogVisible: false,
      // 
      registruleForm: {
        adminname: '',
        password: '',
        role: ''
      },
      // 
      rules: {
        adminname: [
          { required: true, message: '', trigger: 'blur' },
          { require: true, min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { require: true, min: 3, max: 8, message: ' 3  8 ', trigger: 'blur' }
        ],
        role: [
          { required: true, message: '', trigger: 'blur' },
          { require: true, min: 1, max: 1, message: '1,2', trigger: 'blur' }
        ]
      },
      // 
      adminList: [],
      // 
      total: 0,
      currentpage: 0,
      pagenum: 0,
      adminId: 0
    }
  },
  methods: {
    // 
    async getAllAdminInfo () {
      const { data: res } = await this.$http.post('admin/getalladmin', {
        pagenum: this.pagenum
      })
      if (res.meta.status !== 200) {
        return this.$message.success('')
      }
      this.adminList = res.data.adminList
      this.total = res.data.total
    },
    handleCurrentChange (num) {
      this.pagenum = num
      this.getAllAdminInfo()
    },
    handleClose () {
    },
    //  
    async registadmin () {
      // 
      this.dialogVisible = true
    },
    // 
    addAdmins () {
      this.$refs.ruleForm.validate(async valid => {
        console.log(this.$refs.ruleForm)
        if (!valid) {
          this.$refs.ruleForm.resetFields()
          return this.$message.error('')
        } else {
          if (this.registruleForm.adminname.trim() === '' || this.registruleForm.password.trim() === '') {
            return this.$message.error('')
          }
          const { data: res } = await this.$http.post('admin/regist', {
            adminname: this.registruleForm.adminname,
            password: this.registruleForm.password,
            role: this.registruleForm.role
          })
          if (res.meta.status !== 200) {
            return this.$message.error('')
          }
          this.$message.success('')
          this.dialogVisible = false
        }
        this.dialogVisible = false
      })
    }
  },
  created () {
    this.getAllAdminInfo()
  }
}
</script>
<style scoped>
.bread {
  margin-bottom: 20px;
}
</style>
