<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right" class="bread">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <div style="margin: 15px; width: 300px">
      <el-input
        placeholder=""
        v-model="goodid"
        class="input-with-select"
      >
        <el-button
          slot="append"
          icon="el-icon-search"
          @click="serchid"
        ></el-button>
      </el-input>
    </div>
    <!--  -->
    <el-table :data="goodsList" style="width: 100%" stripe height="600px">
      <el-table-column
        prop="goodsId"
        label="ID"
        width="100px"
      ></el-table-column>
      <el-table-column prop="companyName" label=""></el-table-column>
      <el-table-column prop="supply" label=""></el-table-column>
      <el-table-column prop="proArea" label=""></el-table-column>
      <el-table-column
        prop="goodsPrice"
        label="/"
        width="130px"
      ></el-table-column>
      <el-table-column prop="transport" label=""></el-table-column>
      <el-table-column
        prop="transPrice"
        label="/"
        width="130px"
      ></el-table-column>
      <el-table-column prop="quality" label=""></el-table-column>
      <el-table-column
        prop="qualityCertificate"
        label=""
        width="300px"
      >
        <template slot-scope="scope">
          <el-link
            type="primary"
            @click="downloadmyfile(scope.row.qualityCertificate)"
            >{{ scope.row.qualityCertificate }}</el-link
          >
        </template>
      </el-table-column>
      <el-table-column prop="state" label="">
        <template slot-scope="scope">
          <el-tag type="danger" v-if="scope.row.state === '1'"></el-tag>
          <el-tag type="success" v-else></el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="opinion" label=""></el-table-column>
      <el-table-column label="">
        <template slot-scope="scope"
          ><el-button
            type="primary"
            icon="el-icon-s-goods"
            size="mini"
            :disabled="scope.row.state === '1'"
            @click="buygoods(scope.row.goodsId)"
            ></el-button
          ></template
        >
      </el-table-column>
    </el-table>
    <div class="block">
      <el-pagination
        layout="prev, pager, next"
        :page-size="2"
        :total="total"
        @current-change="handleCurrentChange"
        :current-page="currentpage"
      >
      </el-pagination>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      goodid: '',
      goodsList: [],
      //  http://127.0.0.1:8081/file/download?fileName=11.zip
      total: 0,
      currentpage: 0,
      pagenum: 0,
      dialogFormVisible: false,
      form: {
        info: ''
      },
      // true 
      value: true,
      // ID
      checkId: 0
    }
  },
  methods: {
    // 
    async getList () {
      if (this.goodid === '') {
        return this.getAllList()
      }
      // 
      const { data: res } = await this.$http.post('goods/getallgoodstouser',
        {
          pagenum: 1,
          company: this.goodid
        })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.goodsList = res.data.goodsList
      this.total = res.data.total
    },
    handleCurrentChange (num) {
      this.pagenum = num
      if (this.goodid === '') {
        return this.getAllList()
      }
      this.getList()
    },
    serchid () {
      this.getList()
    },
    // 
    async getAllList () {
      // 
      const { data: res } = await this.$http.post('goods/getallgoods',
        {
          pagenum: this.pagenum
        })
      if (res.meta.status !== 200) {
        return this.$message.error('')
      }
      this.goodsList = res.data.goodsList
      this.total = res.data.total
    }
  },
  created () {
    if (window.sessionStorage.getItem('loginstate') !== '1') {
      this.$router.push('/')
      return this.$message.error(',')
    }
    this.getAllList()
  }
}
</script>
<style scoped>
.bread {
  margin-bottom: 20px;
}
</style>
