<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right" class="bread">
      <el-breadcrumb-item :to="{ path: '/home' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <!--  -->
    <div class="formarea">
      <el-form
        :model="ruleForm"
        :rules="rules"
        ref="ruleForm"
        label-width="100px"
        class="demo-ruleForm"
      >
        <el-form-item label="" prop="companyname">
          <el-input v-model="ruleForm.companyname"></el-input>
        </el-form-item>
        <el-form-item label="" prop="companysamplename">
          <el-input v-model="ruleForm.companysamplename"></el-input>
        </el-form-item>
        <el-form-item label="" prop="legalperson">
          <el-input v-model="ruleForm.legalperson"></el-input>
        </el-form-item>
        <el-form-item label="" prop="registpart">
          <el-input v-model="ruleForm.registpart"></el-input>
        </el-form-item>
        <el-form-item label="/" prop="registmoney">
          <el-input v-model="ruleForm.registmoney" type="number"></el-input>
        </el-form-item>
        <el-form-item label="" prop="companycontacts">
          <el-input v-model="ruleForm.companycontacts"></el-input>
        </el-form-item>
        <el-form-item label="" prop="companyphone">
          <el-input v-model="ruleForm.companyphone"></el-input>
        </el-form-item>
        <el-form-item label="" prop="companyfax">
          <el-input v-model="ruleForm.companyfax"></el-input>
        </el-form-item>
        <el-form-item label="" prop="companyemail">
          <el-input v-model="ruleForm.companyemail"></el-input>
        </el-form-item>
        <el-form-item label="" prop="companylicense">
          <el-input v-model="ruleForm.companylicense"></el-input>
        </el-form-item>
        <el-form-item label="" prop="companytax">
          <el-input v-model="ruleForm.companytax"></el-input>
        </el-form-item>
        <el-form-item label="" prop="companynum">
          <el-input v-model="ruleForm.companynum"></el-input>
        </el-form-item>
        <el-form-item label="" prop="companybank">
          <el-input v-model="ruleForm.companybank"></el-input>
        </el-form-item>
        <el-form-item label="" prop="companybanknum">
          <el-input v-model="ruleForm.companybanknum"></el-input>
        </el-form-item>
        <el-form-item label="" prop="companypermit">
          <el-input v-model="ruleForm.companypermit"></el-input>
        </el-form-item>
        <el-form-item label="" prop="companyperson">
          <el-input v-model="ruleForm.companyperson"></el-input>
        </el-form-item>
        <el-form-item label="" prop="companysituation">
          <el-input
            v-model="ruleForm.companysituation"
            type="textarea"
            rows="4"
          ></el-input>
        </el-form-item>
        <el-form-item label="" prop="companytransport">
          <el-input v-model="ruleForm.companytransport"></el-input>
        </el-form-item>
        <el-form-item label="" prop="companyintroduce">
          <el-input v-model="ruleForm.companyintroduce"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="submitForm('ruleForm')"
            ></el-button
          >
          <el-button @click="resetForm('ruleForm')"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      ruleForm: {
        companyname: '',
        companysamplename: '',
        legalperson: '',
        registpart: '',
        registmoney: '',
        companycontacts: '',
        companyphone: '',
        companyfax: '',
        companyemail: '',
        companylicense: '',
        companytax: '',
        companynum: '',
        companybank: '',
        companybanknum: '',
        companypermit: '',
        companyperson: '',
        companysituation: '',
        companytransport: '',
        companyintroduce: ''
      },
      rules: {
        companyname: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 15, message: ' 3  10 ', trigger: 'blur' }
        ],
        companysamplename: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }],
        legalperson: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
        ],
        registpart: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
        ],
        registmoney: [
          { required: true, message: '/', trigger: 'blur' }
        ],
        companycontacts: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
        ],
        companyphone: [
          { required: true, message: '', trigger: 'blur' },
          {
            required: true,
            validator: this.checkPhone,
            trigger: 'blur'
          }
        ],
        companyfax: [{ required: true, message: '', trigger: 'blur' }],
        companyemail: [{ required: true, message: '', trigger: 'blur' }],
        companylicense: [{ required: true, message: '', trigger: 'blur' }],
        companytax: [{ required: true, message: '', trigger: 'blur' }],
        companynum: [{ required: true, message: '', trigger: 'blur' }],
        companybank: [{ required: true, message: '', trigger: 'blur' }],
        companybanknum: [{ required: true, message: '', trigger: 'blur' }],
        companypermit: [{ required: true, message: '', trigger: 'blur' }],
        companyperson: [{ required: true, message: '', trigger: 'blur' }],
        companysituation: [{ required: true, message: '', trigger: 'blur' }],
        companytransport: [{ required: true, message: '', trigger: 'blur' }],
        companyintroduce: [{ required: true, message: '', trigger: 'blur' }]
      }
    }
  },
  methods: {
    submitForm (formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid) {
          const { data: res } = await this.$http.post('company/addcompany', {
            companyname: this.companyname,
            companysamplename: this.companysamplename,
            legalperson: this.legalperson,
            registpart: this.registpart,
            registmoney: this.registmoney,
            companycontacts: this.companycontacts,
            companyphone: this.companyphone,
            companyfax: this.companyfax,
            companyemail: this.companyemail,
            companylicense: this.companylicense,
            companytax: this.companytax,
            companynum: this.companynum,
            companybank: this.companybank,
            companybanknum: this.companybanknum,
            companypermit: this.companypermit,
            companyperson: this.companyperson,
            companysituation: this.companysituation,
            companytransport: this.companytransport,
            companyintroduce: this.companyintroduce
          })
          if (res.meta.status !== 200) {
            return this.$message.error('')
          }
          this.resetForm()
          return this.$message.success('')
        } else {
          this.$message.info('')
          return false
        }
      })
    },
    resetForm (formName) {
      this.$refs[formName].resetFields()
    },
    // 
    checkPhone (rule, value, callback) {
      if (!value) {
        callback()
        return callback(new Error(''))
      } else {
        const reg = /^1[3|4|5|7|8|9][0-9]\d{8}$/
        console.log(reg.test(value))
        if (reg.test(value)) {
          callback()
        } else {
          return callback(new Error(''))
        }
      }
    }
  },
  created () {
    if (window.sessionStorage.getItem('loginstate') !== '1') {
      this.$router.push('/')
      return this.$message.error(',')
    }
  }
}
</script>
<style scoped>
.bread {
  margin-bottom: 20px;
}
.formarea {
  width: 800px;
  margin: 0 auto;
}
</style>
