<template>
  <div>
    <!--  -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/index' }"></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
      <el-breadcrumb-item></el-breadcrumb-item>
    </el-breadcrumb>
    <div id="formarea">
      <!--  -->
      <el-form
        ref="formref"
        :model="form"
        label-width="200px"
        :rules="formrules"
      >
        <el-form-item label="" prop="name">
          <el-input v-model="form.name" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="" prop="region">
          <el-input v-model="form.region" show-word-limit></el-input>
        </el-form-item>
        <el-form-item label="" prop="desc">
          <el-input
            type="textarea"
            v-model="form.desc"
            :rows="20"
            show-word-limit
            clearable
          ></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="onSubmit"></el-button>
          <el-button @click="clearinfo"></el-button>
        </el-form-item>
      </el-form>
    </div>
  </div>
</template>
<script>
export default {
  data () {
    return {
      form: {
        name: '',
        region: '',
        desc: ''
      },
      formrules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { require: true, min: 1, max: 20, message: '20', trigger: 'blur' }
        ],
        region: [
          { required: true, message: '', trigger: 'blur' },
          { require: true, min: 1, max: 20, message: '20', trigger: 'blur' }
        ],
        desc: [
          { required: true, message: '', trigger: 'blur' }
        ]

      }
    }
  },
  methods: {
    // 
    async onSubmit () {
      this.$refs.formref.validate(async (valid) => {
        if (!valid) {
          this.$refs.ruleForm.resetFields()
          return this.$message.error('')
        } else {
          const { data: res } = await this.$http.post('infor/add', {
            informationtitle: this.form.name,
            informationsample: this.form.region,
            informationcontent: this.form.desc
          })
          if (res.meta.status !== 200) {
            return this.$message.error('')
          }
          this.$refs.formref.resetFields()
          return this.$message.warning('')
        }
      })
    },
    clearinfo () {
      this.$refs.formref.resetFields()
    }
  },
  created () {
    if (window.sessionStorage.getItem('loginstate') !== '1') {
      this.$router.push('/')
      return this.$message.error(',')
    }
  }
}
</script>
<style scoped>
#formarea {
  width: 800px;
  height: 600px;
  margin-top: 40px;
  margin-left: 400px;
  margin-bottom: 50px;
}
</style>
