<template>
  <div id="back">
    <div class="loginform">
      <!--  -->
      <img src="../assets/162632604839859.png" alt="" />
      <div></div>
      <!--  -->
      <div id="formdata">
        <span></span
        ><el-input
          v-model="username"
          placeholder=""
          clearable
        ></el-input
        ><br />
        <span></span>
        <el-input
          v-model="password"
          placeholder=""
          clearable
          type="password"
        ></el-input>
      </div>
      <!--  -->
      <div class="block" style="width: 200px">
        <el-slider v-model="value"></el-slider>
      </div>
      <el-link type="primary" icon="el-icon-edit" @click="registadmin"
        ></el-link
      >
      <!--  -->
      <div class="btnclass">
        <el-button type="primary" @click="adminlogin" :autofocus="true"
          ></el-button
        >
        <el-button type="primary" @click="userlogin"></el-button>
      </div>
    </div>
    <!--   -->
    <el-dialog
      title=""
      :visible.sync="dialogVisible"
      width="30%"
      :before-close="handleClose"
      ><span>
        <el-form
          :model="registruleForm"
          :rules="rules"
          ref="ruleForm"
          label-width="180px"
          class="demo-ruleForm"
        >
          <el-form-item label="" prop="username">
            <el-input
              v-model="registruleForm.username"
              class="reginput"
            ></el-input>
          </el-form-item>
          <el-form-item label="" prop="password">
            <el-input
              v-model="registruleForm.password"
            ></el-input> </el-form-item></el-form
      ></span>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false"> </el-button>
        <el-button type="primary" @click="registeruser"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data () {
    return {
      value: 0,
      username: '',
      password: '',
      // 
      dialogVisible: false,
      // 
      registruleForm: {
        username: '',
        password: ''
      },
      // 
      rules: {
        username: [
          { required: true, message: '', trigger: 'blur' },
          { require: true, min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { require: true, min: 3, max: 8, message: ' 3  8 ', trigger: 'blur' }
        ]
      }
    }
  },
  methods: {
    //   
    async adminlogin () {
      if (this.value !== 100) {
        this.value = 0
        return this.$message.info('')
      }
      const { data: res } = await this.$http.post('admin/login', {
        adminname: this.username.trim(),
        password: this.password.trim()
      })
      if (res.meta.status !== 200) {
        this.username = ''
        this.password = ''
        this.value = 0
        this.$message.error(',')
        return ''
      }
      this.$message.success('')
      this.value = 0
      // session
      window.sessionStorage.setItem('username', res.data.admin.adminName)
      window.sessionStorage.setItem('loginstate', '1')
      // 
      this.$router.push('/adminhome')
    },
    // 
    async userlogin () {
      if (this.value !== 100) {
        this.value = 0
        return this.$message.info('')
      }
      const { data: res } = await this.$http.post('user/login', {
        username: this.username,
        password: this.password
      })
      if (res.meta.status !== 200) {
        this.username = ''
        this.password = ''
        this.value = 0
        return this.$message.error('')
      }
      console.log(res.data.user.userState)
      if (res.data.user.userState !== 'true') {
        return this.$message.warning('')
      }
      console.log(res.data)
      this.$message.success('')
      window.sessionStorage.setItem('username', res.data.userName)
      window.sessionStorage.setItem('userrole', res.data.userRole)
      window.sessionStorage.setItem('userid', res.data.userid)
      window.sessionStorage.setItem('loginstate', '1')
      this.value = 0
      return this.$router.push('/home')
    },
    //  
    async registadmin () {
      // 
      this.dialogVisible = true
    },
    // 
    registeruser () {
      this.$refs.ruleForm.validate(async valid => {
        if (!valid) {
          this.$refs.ruleForm.resetFields()
          return this.$message.error('')
        } else {
          if (this.registruleForm.username.trim() === '' || this.registruleForm.password.trim() === '') {
            return this.$message.error('')
          }
          const { data: res } = await this.$http.post('user/regist', {
            username: this.registruleForm.username,
            password: this.registruleForm.password
          })
          if (res.meta.status !== 200) {
            return this.$message.error('')
          }
          this.$message.success('')
          this.dialogVisible = false
        }
        this.dialogVisible = false
      })
    },
    handleClose () {
    }
  }
}
</script>

<style scoped>
#back {
  position: fixed;
  background: url('../assets/background .jpg');
  width: 100%;
  height: 100%;
  background-size: 100%;
}
.el-link {
  position: relative;
  float: left;
  top: 100px;
  left: 440px;
}
.btnclass {
  position: relative;
  top: 140px;
  right: 40px;
}
.el-button {
  margin: 0 60px;
}
.loginform {
  width: 600px;
  height: 400px;
  margin: 250px auto;
  border: 1px solid #eee;
  box-shadow: 5px 5px 20px;
}
#formdata {
  position: absolute;
  top: 200px;
  width: 300px;
  height: 100px;
  margin: 200px 150px 0px 150px;
}
.el-input {
  width: 240px;
  margin-bottom: 15px;
}
img {
  position: relative;
  left: 70px;
  top: 15px;
}
.block {
  width: 200px;
  position: relative;
  top: 130px;
  left: 190px;
}
</style>
